<template>
  <div>
  <router-link to="/" class="button is-success mt-5">Go to Home</router-link>
  <table class="table is-striped is-bordered mt-2 is-fullwidth">
      <thead>
        <tr>
          <th>Titulo</th>
          <th>Completado</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="item in items" :key="item.id">
          <td class="has-text-left">{{ item.title }}</td>
          <td class="has-text-right">{{ item.completed }}</td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
// import axios
import axios from "axios";
 
export default {
  name: "ProductList",
  data() {
    return {
      items: [],
    };
  },
 
  created() {
    this.getProducts();
  },
 
  methods: {
    // Get All Products
    async getProducts() {
      try {
        //const response = await axios.get("http://localhost:5000/categoria");
        const response = await axios.get("https://jsonplaceholder.typicode.com/todos/");
        this.items = response.data;
      } catch (err) {
        console.log(err);
      }
    },
 
    
  },
};
</script>

<style>
@import ~bulma/css/bulma.css;
</style>